//
// Imports
//
import { setCookie } from '../site-utils.js';

const $ = jQuery;

//
// Turn off animation stuff
//
const turnOff = () => {
  $('html').addClass('site-no-animation');
  //pause playing videos.
  $('.js--video-play-control.is-playing').trigger('click');
}

//
// Turn on animation stuff
//
const turnOn = () => {
  $('html').removeClass('site-no-animation');
  $('.js--video-play-control:not(.is-playing)').trigger('click');
}

//
// Open the options menu
//
let focusableEls, firstFocusableEl, lastFocusableEl;

const openOptions = (focus) => {

  const $opts = $('#js__site-options');

  $opts.addClass('show-options').attr('aria-modal', true);

  $('#js--site-options-close-first').focus();

  setTimeout(() => {
    $opts.addClass('animates-options');

    if ( focus === true ) {
      $opts.find('h2')
        .first()
        .focus();
    }
  }, 20);

  window.siteHasOpenOptions = true;

  // Setup for tab trap
  focusableEls = $opts.find('a, object, :input, iframe, [tabindex]');
  firstFocusableEl = focusableEls.first()[0];
  lastFocusableEl = focusableEls.last()[0];

}


//
// Close the options menu
//
export const closeOptions = (focus) => {

  const $opts = $('#js__site-options');

  $opts.removeClass('animates-options').attr('aria-modal', false);

  if ( focus === true ) {
    $('#js__toggle-options').focus();
  }

  setTimeout(() => {
    $opts.removeClass('show-options');
  }, 320);

  window.siteHasOpenOptions = false;
  focusableEls = null;
  firstFocusableEl = null;
  lastFocusableEl = null;
}


//
// Options init
//
export const navOptions = () => {

  // Open the options
  $('#js__toggle-options').on('click', function () {
    // Close modals if they're open. see navigation-modal.js
    if (window.siteHasOpenModal) {
      $('.modal.modal__open').find('.modal--close').first().trigger('click');
    }
    $('#js__site-options').hasClass('show-options') ? closeOptions() : openOptions(true);
  });

  // Close the options if user leaves options panel
  $('#js__site-options').on('mouseleave', function() {
    if(!$('html').hasClass('keyboard-user')){
      closeOptions();
    }
  });

  // Close the options if mouseover button
  $('.modal--open.search, .modal--open.more').on('mouseenter', function() {
    closeOptions();
  });

  // Click close
  $('.site-options__close').on('click', function(e) {
    closeOptions(true);
  })



  //
  // Toggle Option Button
  //
  $('#js--site-options-animations').on('click', function() {
    const $this = $(this),
          setTo = $this.attr('data-setoptionto');

    // Set cookie value for one year
    setCookie('sitePreventAnimations', setTo, '365');
    $this.toggleClass('is-on is-off');

    // Turn animations and videos on or off
    if (setTo === 'prevent') {
      $this.attr('data-setoptionto', 'allow').attr('aria-pressed', 'true');
      turnOff();
    } else {
      $this.attr('data-setoptionto', 'prevent').attr('aria-pressed', 'false');
      turnOn();
    }
  });



  //
  // Modal Key Functions
  // Enact tab trap
  //
  $(window).on('keydown', function(e) {

    // Allow Escape to close window modal
    if (e.keyCode === 27  ) {
      if ( window.siteHasOpenOptions ) {
        closeOptions();
      }

    // Tab Trap on open modals
    } else if (
      (e.key === 'Tab' || e.keyCode === 9) &&
      window.siteHasOpenOptions === true &&
      ($('#js__site-options').attr('aria-modal') === 'true')
    ) {

      // shift + tab
      if ( e.shiftKey ) {
        if (document.activeElement === firstFocusableEl) {
          lastFocusableEl.focus();
          e.preventDefault();
        }
      // tab
      } else {
        if (document.activeElement === lastFocusableEl) {
          firstFocusableEl.focus();
          e.preventDefault();
        }
      }
    }
  });

}



