//
// Imports
//
import * as navutils from './navigation-utils.js';

const $ = jQuery;
const $menu        = jQuery('#site-menu'),
      $parentLinks = $menu.find('> li > a, > li > button');
let   pauseLeave   = false,
      parentEnter  = false;




//
// Default Export
//
export const navSubmenus = () => {

  //
  // Hide submenu if you click anywhere
  //
  $('body').on('click', function(e) {
    if (e.target.id === 'site-menu' || $(e.target).closest('#site-menu').length) {
      return;
    }
    navutils.closeSubNavs();
  });


  //
  // Hide submenu if you mouseout
  //
  $('.submenu').on('mouseleave', function(e) {
    // Immediately close if hovering the main menu
    if ($(e.relatedTarget).hasClass('site-header__mask')) {
      navutils.closeSubNavs(false, true);
    } else {
      // Give a few milliseconds to confirm user meant to leave
      pauseLeave = false;
      setTimeout(function () {
        if (!pauseLeave) {
          navutils.closeSubNavs(false, true);
        }
      }, 300);
    }
  });


  //
  // Prevent submenu close if you reenter
  //
  $('.submenu').on('mouseenter', function () {
    pauseLeave = true;
  });


  //
  // Parent link hover in
  //
  $parentLinks.on('mouseenter', function () {
    const el = $(this);
    parentEnter = true;

    setTimeout(function () {
      navutils.closeSubNavs(el);

      if (parentEnter) {
        navutils.openSubNav(el);
      }

    }, 125);
  })
    .on('touchstart', function (e) {
      const el = $(this);

      if (el.attr('data-touched') !== 'true') {
        e.preventDefault();
        navutils.closeSubNavs(el);
        navutils.openSubNav(el);
        el.attr('data-touched', 'true');
      }
    });


  //
  // Parent link hover out
  //
  $parentLinks.on('mouseleave', function () {
    parentEnter = false;
  })


  //
  // Parent link hover out
  //
  $parentLinks.on('focus', function () {
    const el = $(this);
    navutils.closeSubNavs(el);
    navutils.openSubNav(el);
  });

  $('#js__toggle-more').on('focus', function() {
    navutils.closeSubNavs();
  });

  // Note: padLeft is disabled, it's not needed, have no idea why it was done this way.
  //
  // Set padding on subnav menus to match parent link position
  //
  // $(window).on('load', function () {
  //   navutils.padLeft();
  // });


  //
  // Reset padding resize
  //
  // $(window).on('resize', function () {
  //   navutils.padLeft();
  // });


  //
  // Responsive Navigation Buttons to display submenus
  //
  $(document).on('click', '.flyout__main-nav-trigger', function () {
    const $this = $(this);
    const menu = $this.next('ul');
    let toHeight;

    if ($this.hasClass('open-submenu')) {
      $this.attr('aria-expanded', 'false');
      menu.animate({
        'height': 0
      }, 400, function() {
        menu.removeClass('is-shown');
      });
    } else {
      menu.addClass('is-shown');
      menu.height('auto');
      toHeight = menu.height();
      menu.height(0);
      $this.attr('aria-expanded', 'true');
      menu.animate({
        'height': toHeight
      }, 400);
    }

    $this.toggleClass('open-submenu');
  });

}



