const $ = jQuery;

//
// Basic Page Sub Nav
//
export const subNav = () => {

  // Toggle sub nav display on mobile devices
  $('.c-sub-nav__button').on('click', function() {
    const $this = $(this);
    $this.toggleClass('menu-open').next('.c-sub-nav__content').slideToggle(300);
  });

  // Close the sub nav on mobile devices by clicking anywhere
  $('body').click(function (e) {
    if (!$(e.target).closest('.c-sub-nav').length) {
      $('.c-sub-nav__button.menu-open').trigger('click');
    }
  });

}



