//
// Imports
//
import inView from '../vendor/in-view.js';
import '../vendor/parallax-element.js';
import { tablesInit } from './tables.js';


const $ = jQuery;


//
// Exports
//

//
// Set Cookie Utility
//
export function setCookie(name, value, days) {
  let expires = "";
  if (days) {
    let date = new Date();
    date.setTime(date.getTime() + (days * 24 * 60 * 60 * 1000));
    expires = "; expires=" + date.toUTCString();
  }
  document.cookie = name + "=" + (value || "") + expires + "; path=/";
}

//
// Get Cookie Utility
//
export function getCookie(name) {
  let nameEQ = name + "=";
  let ca = document.cookie.split(';');
  for (let i = 0; i < ca.length; i++) {
    let c = ca[i];
    while (c.charAt(0) == ' ') c = c.substring(1, c.length);
    if (c.indexOf(nameEQ) == 0) return c.substring(nameEQ.length, c.length);
  }

  return false;
}


//
// Delete Cookie Utility
//
export function eraseCookie(name) {
  document.cookie = name + '=; Max-Age=-99999999;';
}





//
// Initialize site wide default functionality
//
export const initSiteDefaults = () => {

  // Check for Site Animations
  if (getCookie('sitePreventAnimations') === 'prevent') {
    $('html').addClass('site-no-animation');
    $('#js--site-options-animations').removeClass('is-on').addClass('is-off').attr('data-setoptionto', 'allow').attr('aria-pressed', 'true');
  }

  // Initialize in-view.js on classes with '.u-watch'
  inView('.u-watch')
    .on('enter', el => {
      el.classList.add('u-in-view');
    });

  // Initialize parallax on classes
  $('.js-parallax-element').parallaxElement({
    disableMobile: true
  });

  $('.js-parallax-element-max').parallaxElement({
    setMax: 0.5,
    disableMobile: true
  });


  //
  // Add Keyboard Class for styling focus states when user tabs down
  //
  function handleFirstTab(e) {
    if (e.keyCode === 9) {
      document.documentElement.classList.add('keyboard-user');
      window.removeEventListener('keydown', handleFirstTab);
    }
  }

  window.addEventListener('keydown', handleFirstTab);


  //
  // Detect IE
  //
  if (window.navigator.userAgent.indexOf("Trident/7.0") > 0) {
    $('body').addClass('ie-11');
  }


  //
  // On Load Functions
  //
  $(window).on('load', function() {

    // Add u-watch class to all items above the viewport
    $('.u-watch').each(function() {
      let $this = $(this);
      if (($this.offset().top - $(window).scrollTop()) - $(window).height() < 0) {
        $this.addClass('u-in-view');
      }
    });


    //
    // Drop the last word in a line before a span if
    // the span is on it's own line. Works on load only.
    //
    // Example usage: line span in Feature Stories module
    //
    $('.js-connect-last-word').each(function () {
      const $this = $(this);
      const $parent = $this.parent();
      const thisOffset = $this.offset().left - $parent.offset().left;

      if (thisOffset <= 20) {
        let parentString = $parent.html();
        let parentData = $.parseHTML(parentString)[0].data;
        let parentDataAlt = parentData.replace(/ ([^ ]*)$/, '<br />$1');
        var dataRegExp = new RegExp(parentData, "g");
        let newParent = parentString.replace(dataRegExp, parentDataAlt);
        $parent.html(newParent);
      }

      return;
    });


    //
    // Table sorting and responsive layout
    //
    tablesInit();

  });

}



