//
// Imports
//
import { navSubmenus } from './navigation-submenu.js';
import { navKeyboardControl } from './navigation-keyboard.js';
import { modalNavigation } from './navigation-modal.js';
import { scrollNav } from './navigation-scroll.js';
import { navSliders } from './navigation-sliders.js';
import { navOptions } from './navigation-options.js';


//
// Init all navigation scripts
//
export const navigationInit = () => {

  navSubmenus();        // Enable Submenu Display
  navKeyboardControl(); // Enable Keyboard control
  modalNavigation();    // Enable Modals
  scrollNav();          // Enable Scrolling Header
  navSliders();         // Enable Navigation Sliders on mobile
  navOptions();         // Accessibility Options Menu

}



