const $ = jQuery;
const $drawer      = $('#site-menu-drawer'),
      hoverClass   = 'show-menu',
      activeParent = 'active-parent';

let activeClose, activeOpen;

//
// Constant storing key numbers as words to make things easier
//
export const KEYED = {
  left: 37,
  up: 38,
  right: 39,
  down: 40,
  esc: 27,
  space: 32,
  return: 13,
  tab: 9,
  alpha: {
    48: "0", 49: "1", 50: "2", 51: "3", 52: "4", 53: "5", 54: "6", 55: "7",
    56: "8", 57: "9", 59: ";", 65: "a", 66: "b", 67: "c", 68: "d", 69: "e",
    70: "f", 71: "g", 72: "h", 73: "i", 74: "j", 75: "k", 76: "l", 77: "m",
    78: "n", 79: "o", 80: "p", 81: "q", 82: "r", 83: "s", 84: "t", 85: "u",
    86: "v", 87: "w", 88: "x", 89: "y", 90: "z", 96: "0", 97: "1", 98: "2",
    99: "3", 100: "4", 101: "5", 102: "6", 103: "7", 104: "8", 105: "9"
  }
}





//
// Open target subnav
//
export const openSubNav = (trigger) => {

  const target = trigger.next('.submenu');

  if (target.length) {

    const thisChild = target.attr('id');

    // Don't again on open menus
    if (typeof activeOpen !== 'undefined' && thisChild === activeOpen) {
      activeOpen = thisChild;
      return false;
    }

    activeOpen = thisChild;

    // Clear existing animations
    clearInterval(window.linkSlideInterval);

    // Get height for drawer to animate to
    target.show();
    let targetHeight = target.outerHeight();
    let targetImgHeight = (target.find('img').outerHeight()) + (parseInt(target.css('padding-top')) + parseInt(target.css('padding-bottom')));
    target.hide();

    // Remove animation class on links
    target.find('.animate-in').removeClass('animate-in');

    // Add animate classes to wrappers
    trigger.addClass(activeParent);

    target.addClass(hoverClass);
    setTimeout(() => {
      target.addClass('animates-menu-in');
    }, 20);

    // Animate drawer to height
    let dh = targetImgHeight > targetHeight ? targetImgHeight : targetHeight;

    if ($('html').hasClass('site-no-animation')) {
      $drawer.stop().height(dh);
    } else {
      $drawer.stop().animate({
        height: dh,
      }, 300);
    }


    // Animate links in, using interval to step transtions out
    let $links = target.find('a');
    let count = $links.length;
    let step = 0;

    window.linkSlideInterval = setInterval(function () {
      $($links[step]).stop().addClass('animate-in');
      step++;
      if (step === count) {
        clearInterval(window.linkSlideInterval);
        return;
      }
    }, 95);
  }

}





//
// Close all subnavs
//
export const closeSubNavs = (el, clear) => {

  // Do not close an active menu
  if (typeof el !== 'undefined' && el !== false) {
    const thisChild = el.next('.submenu').attr('id');

    if (typeof activeClose == 'undefined' || thisChild === activeClose) {
      activeClose = thisChild;
      return false;
    }

    activeClose = thisChild;

  }

  $('.submenu.show-menu').removeClass('animates-menu-in').removeClass(hoverClass);

  const $activeParent = $('.' + activeParent);

  if ($activeParent.attr('data-touched') === 'true') {
    $activeParent.removeAttr('data-touched');
  }

  $activeParent.removeClass(activeParent);

  if ($('html').hasClass('site-no-animation')) {
    $drawer.stop().height(0);
  } else {
    $drawer.stop().animate({
      height: 0,
    }, 350);
  }

  if (clear) {
    activeOpen = undefined;
  }

}





//
// Add Padding to Sub-Menus equal to the position of the parent menu item
//
export const padLeft = () => {

  if ($('.site-header').length > 0) {
    $('.site-header__main-nav .submenu').each(function () {
      let $this = $(this);
      $this.find('.submenu__inner')
        .css({
          'padding-left': ($this.prev('a').offset().left - $('.site-header__logo').offset().left)
        });
    });
  }

}





//
// Go to next Top Level link or button
//
export const focusNextTopLevelLink = (thisTopLevelItem) => {

  // If there is a top level neighbor next door
  if (thisTopLevelItem.next('li').length > 0) {
    thisTopLevelItem.next('li').find('a, button').first().focus();
  } else {
    thisTopLevelItem.parent('ul').next('ul').find('a, button').first().focus();
  }
}





//
// Go to previous Top Level link or button
//
export const focusPrevTopLevelLink = (thisTopLevelItem) => {

  // If there is a top level neighbor next door
  if (thisTopLevelItem.prev('li').length > 0) {
    thisTopLevelItem.prev('li').find('a, button').first().focus();
  } else {
    thisTopLevelItem.parent('ul').prev('ul').find('a, button').last().focus();
  }
}



