//
// Imports
//
import '../../vendor/slick.js';

const $ = jQuery;

//
// Setup slider for Guides
const guideSlider = () => {

  const $w             = $(window),
        guidesCarousel = $('.guide-carosel__guides'),
        arrowright     = $('.guide-carosel__button--prev'),
        arrowleft      = $('.guide-carosel__button--next');

  // Destroy slider below 750
  if ($w.width() < 750) {
    if (guidesCarousel.hasClass('slick-initialized')) {
      return guidesCarousel.slick('unslick');
    }
    return;
  }

  // Initialize slider if it has not already been
  if (!guidesCarousel.hasClass('slick-initialized')) {
    guidesCarousel.slick({
      variableWidth: true,
      slidesToShow: 2,
      infinite: false,
      prevArrow: arrowright,
      nextArrow: arrowleft
    });
    return;

  // Otherwise update the slider
  } else {
    guidesCarousel.slick('setOption', true);
  }
}


//
// Nav Slider Init
//
export const navSliders = () => {

  guideSlider();

  // Resize needed to determine if slider should be destroyed
  $(window).on('resize', function() {
    guideSlider();
  });

}



