//
// Imports
//
import { List } from '../vendor/list.js';
import '../vendor/tablesaw.js';
import '../vendor/tablesawInit.js';

const $ = jQuery;

export const tablesInit = () => {

  //
  // Sort Tables via list.js
  //
  $('.table--sort').each(function() {
    const thisID = $(this).attr('id') ? $(this).attr('id') : 'table--sort-' + Math.floor(Math.random() * Math.floor(500));
    let sortClasses = [];
    $('tr:first > th', this).each(function(index){
      let sortNum = index + 1;
      sortClasses.push('sort-' + sortNum);
      $(this).html('<span class="table--sort__sort sort" data-sort="sort-' + sortNum + '">' + $(this).html() + '</span>');
    }); //we need a sort to
    $('tr:not(:first)', this).each(function(){
      $('th, td', this).each(function(index){
        let sortNum = index + 1;
        $(this).addClass('sort-' + sortNum);
      })
    });
    $('thead', this).addClass('table__sticky-header');
    $('tbody', this).addClass('list');
    $(this).attr('id', thisID);
    let options = {
      valueNames: sortClasses
    };
    const sortTable = new List(thisID, options);
  });

}



